* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: inter, sans-serif;
}

nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  box-shadow: 0 5px skyblue;
}

.navheader {
  display: flex;
  margin-left: 4%;
}

.navimage {
  width: 70px;
  padding-right: 10px;
}

.navheader h2 {
  color: rgb(108, 214, 218);
}

.nav-item {
  list-style: none;
  display: flex;
}

.nav-item :hover {
  color: skyblue;
  border-bottom: 2px solid skyblue;
}

.nav-item li {
  padding: 10px;
}

.main-content {
  margin: 30px;
  align-items: center;
}

.main-facts li {
  padding-block: 10px;
}

.main-facts li::marker {
  font-size: 1.5em;
  color: rgb(108, 214, 218);
}

footer {
  position: fixed;
  bottom: 0px;
  width: 100%;
  background-color: lightskyblue;
  text-align: center;
  padding: 5px;
}